* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Roboto', sans-serif;
}

:root {
    --main-color: #0b0b0b
}

.link {
    color: inherit;
    text-decoration: none;
}

@mixin tablet {
    @media(max-width: 768px) {
        @content;
    }
}

@mixin mobile {
    @media(max-width: 480px) {
        @content;
    }
}